import { Box, IconButton, InputAdornment, Stack, TextField, Typography } from "@mui/material"
import { useEffect, useState } from "react"
import { useRecoilState } from "recoil"
import { userAtom } from "../atoms"
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import FingerprintIcon from '@mui/icons-material/Fingerprint';
import { PHI } from "../constants"
import useCommunication from "../hooks/communication";

const WIDTH = 300
const HEIGHT = WIDTH * PHI

const Login = () => {

    const communication = useCommunication()

    const [_, setUser] = useRecoilState(userAtom)

    const [username, setUsername] = useState('')
    const [usernameError, setUsernameError] = useState(false)
    const [usernameErrorMessage, setUsernameErrorMessage] = useState('')
    const [password, setPassword] = useState('')
    const [passwordError, setPasswordError] = useState(false)
    const [passwordErrorMessage, setPasswordErrorMessage] = useState('')
    const [showPassword, setShowPassword] = useState(false)
    const [message, setMessage] = useState('')

    useEffect(() => {
        if ('speechSynthesis' in window) {
            // console.clear()
            // console.log(speechSynthesis.getVoices().filter(voice => voice.lang === 'es'));
            const text = 'Hola medio puto, ¿cómo estás?';
            const utterance = new SpeechSynthesisUtterance(text);
            utterance.lang = 'es-AR'; // Imposta la lingua italiana

            // Opzionalmente, puoi impostare altre proprietà come il pitch e la velocità
            utterance.pitch = 2; // Da 0 a 2
            utterance.rate = 1; // Da 0.1 a 10

            // Esegui la sintesi vocale
            window.speechSynthesis.speak(utterance);
        } else
            alert('Il tuo browser non supporta la sintesi vocale.');
    }, [])

    const handleUsernameChange = (event: any) => {
        setUsername(event?.target.value)
        setUsernameError(false)
        setUsernameErrorMessage('')
        setMessage('')
    }

    const handlePasswordChange = (event: any) => {
        setPassword(event?.target.value)
        setPasswordError(false)
        setPasswordErrorMessage('')
        setMessage('')
    }

    const handleOnFocus = (event: any) => {
        event.target.select()
    }

    const handleOnKeyDown = (event: any) => {
        if (event.key === 'Enter')
            handleLogInClick()
    }

    const handleLogInClick = () => {
        let continueLogin = true
        if (username === '') {
            setUsernameError(true)
            setUsernameErrorMessage('Chi va là?')
            continueLogin = false
        } else {
            setUsernameError(false)
            setUsernameErrorMessage('')
        }
        if (password === '') {
            setPasswordError(true)
            setPasswordErrorMessage('Parola d\'ordine')
            continueLogin = false
        } else {
            setPasswordError(false)
            setPasswordErrorMessage('')
        }

        if (!continueLogin)
            return

        communication.POST('/login', {
            username: username,
            password: password
        }).then(response => {
            setUser(response.data)
        }).catch(reason => {
            console.log(reason)
            setMessage('Memoria corta ?')
        })
    }

    const handlePasswordClick = () => {
        setShowPassword(!showPassword)
    }

    const inputPropsStyle = {
        endAdornment:
            <InputAdornment position="end">
                <IconButton onClick={handlePasswordClick} size="small">
                    {showPassword ? <Visibility /> : <VisibilityOff />}
                </IconButton>
            </InputAdornment>
    }

    return (
        <Stack
            width={{ xs: '100%', sm: `${WIDTH}px` }}
            height={{ xs: '100%', sm: `${HEIGHT}px` }}
            border={{ sm: '2px solid #1976d2' }}
            borderRadius={7}
            justifyContent={'center'}
            alignItems={'center'}
            boxSizing={'border-box'}
            padding={3}
        >
            <Typography fontSize={'5rem'} variant='h1' color='primary'>Login</Typography>
            <Box flexGrow={1}></Box>
            <TextField
                fullWidth
                autoFocus
                error={usernameError}
                helperText={usernameErrorMessage}
                type="username"
                label="Username"
                variant="standard"
                value={username}
                onChange={handleUsernameChange}
                onFocus={handleOnFocus}
                onKeyDown={handleOnKeyDown}
            />
            <Box flexGrow={.2}></Box>
            <TextField
                fullWidth
                error={passwordError}
                helperText={passwordErrorMessage}
                type={showPassword ? 'text' : 'password'}
                label="Password"
                variant="standard"
                value={password}
                onChange={handlePasswordChange}
                onFocus={handleOnFocus}
                onKeyDown={handleOnKeyDown}
                InputProps={inputPropsStyle}
            />
            <Box flexGrow={1}></Box>
            <Typography color='red'>{message}</Typography>
            <Box>
                <IconButton onClick={handleLogInClick} color="primary">
                    <FingerprintIcon />
                </IconButton>
            </Box>
        </Stack>
    )
}

export default Login