import { AppBar, Box, ButtonGroup, IconButton, Menu, MenuItem, PopoverOrigin, TextField, Toolbar } from "@mui/material";
import { useState } from "react";
import MenuIcon from '@mui/icons-material/Menu';
import ChatIcon from '@mui/icons-material/Chat';
import NotificationsIcon from '@mui/icons-material/Notifications';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import useCommunication from "../../hooks/communication";
import { useRecoilState } from "recoil";
import { userAtom } from "../../atoms";
import { useNavigate } from "react-router-dom";

const anchorOriginStyle: PopoverOrigin = { vertical: 'bottom', horizontal: 'center' }
const transformOriginStyle: PopoverOrigin = { vertical: 'top', horizontal: 'right' }

const NavBar = () => {

    const communication = useCommunication()
    const navigate = useNavigate()

    const [_, setUser] = useRecoilState(userAtom)

    const [anchorAccountEl, setAnchorAccountEl] = useState<null | HTMLElement>(null)
    const [search, setSearch] = useState('')

    const handleMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
        setAnchorAccountEl(event.currentTarget)
    };

    const handleMenuClose = () => {
        setAnchorAccountEl(null)
    }

    const handleSearchChange = (event: any) => {
        setSearch(event.target.value)
    }

    const handleProfile = () => {
        navigate('/profile')
        handleMenuClose()
    }

    const handleLogout = () => {
        communication.GET('/logout')
            .then(() => {
                setUser(undefined)
                navigate('/')
            }).catch(reason => {
                console.log(reason)
            })
    }

    const renderMenu = (
        <Menu
            open={Boolean(anchorAccountEl)}
            anchorEl={anchorAccountEl}
            anchorOrigin={anchorOriginStyle}
            transformOrigin={transformOriginStyle}
            onClose={handleMenuClose}
        >
            <MenuItem onClick={handleProfile}>Profile</MenuItem>
            <MenuItem onClick={handleLogout}>Logout</MenuItem>
        </Menu>
    );

    return (
        <>
            <AppBar component={'nav'} position='static'>
                <Toolbar>
                    <IconButton>
                        <MenuIcon />
                    </IconButton>
                    <Box flexGrow={1} />
                    <TextField
                        autoFocus
                        type="text"
                        label="Search..."
                        value={search}
                        onChange={handleSearchChange}
                    />
                    {/* <Autocomplete
                        renderInput={(function (params: AutocompleteRenderInputParams): ReactNode {
                            return null
                        })}
                        options={[]}
                    /> */}
                    <Box flexGrow={1} />
                    <ButtonGroup>
                        <IconButton>
                            <ChatIcon />
                        </IconButton>
                        <IconButton>
                            <NotificationsIcon />
                        </IconButton>
                        <IconButton onClick={handleMenuOpen}>
                            <AccountCircleIcon />
                        </IconButton>
                    </ButtonGroup>
                </Toolbar>
            </AppBar>
            {renderMenu}
        </>
    );
};

export default NavBar;