import { Box, Button, Stack, TextField } from "@mui/material";
import { useRecoilState } from "recoil";
import { userAtom } from "../atoms";
import { useEffect, useState } from "react";
import { User } from "../interfaces";

const Profile = () => {

    const [user, setUser] = useRecoilState(userAtom)

    // copy user to restore and check differences with the original user
    const [copyUser, setCopyUser] = useState(user)
    // username field
    const [username, setUsername] = useState<string | undefined>(copyUser?.username)
    const [usernameError, setUsernameError] = useState(false)
    const [usernameErrorMessage, setUsernameErrorMessage] = useState('')
    // mail field
    const [mail, setMail] = useState<string | undefined>(copyUser?.mail)
    const [mailError, setMailError] = useState(false)
    const [mailErrorMessage, setMailErrorMessage] = useState('')

    const handleRestore = () => {
        if (copyUser) {
            setUsername(copyUser.username)
            setUsernameError(false)
            setUsernameErrorMessage('')
            setMail(copyUser.mail)
            setMailError(false)
            setMailErrorMessage('')
        }
    }

    const handleUsernameChange = (event: any) => {
        if (event?.target.value === '') {
            setUsernameError(true)
            setUsernameErrorMessage('Non sarai mica Ulisse?')
        }
        else {
            setUsernameError(false)
            setUsernameErrorMessage('')
        }
        setUsername(event?.target.value)
    }

    const handleMailChange = (event: any) => {
        if (event?.target.value === '') {
            setMailError(true)
            setMailErrorMessage('Al momento el Tarado non te lo permette!')
        }
        else {
            setMailError(false)
            setMailErrorMessage('')
        }
        setMail(event?.target.value)
    }

    const handleOnKeyDown = (event: any) => {
        if (event.key === 'Enter')
            handleUpdate()
    }

    const handleUpdate = () => {
        console.log('trying to update')
    }

    return (
        <Stack
            width='100%'
            height='100%'
            justifyContent={'center'}
            alignItems={'center'}
            boxSizing={'border-box'}
            padding={10}
        >
            <TextField
                error={usernameError}
                helperText={usernameErrorMessage}
                type="text"
                label="Username"
                variant="standard"
                value={username}
                onChange={handleUsernameChange}
                onKeyDown={handleOnKeyDown}
            />
            <Box flexGrow={0.1} />
            <TextField
                error={mailError}
                helperText={mailErrorMessage}
                type="mail"
                label="Mail"
                variant="standard"
                value={mail}
                onChange={handleMailChange}
                onKeyDown={handleOnKeyDown}
            />
            <Box flexGrow={0.1} />
            <Button onClick={handleRestore}>Restore</Button>
            <Button onClick={handleUpdate}>Update</Button>
        </Stack>
    );
}

export default Profile;